.top {
  padding: 30px 0;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    padding-top: 150px;
  }
  &__title {
    font-weight: 700;
    font-size: 60px;
    max-width: 600px;
    margin-bottom: 30px;
  }
  &__subtitle {
    font-size: 25px;
    margin-bottom: 60px;
  }
  &__btn {

  }
  &__img {
    max-width: 660px;
  }
}

@media (max-width: 1280px) {
  .top {
    &__content {
      padding-top: 100px;
    }
  }
}

@media (max-width: 1024px) {
  .top {
    &__inner {
      flex-direction: column;
    }
    &__content {
      position: relative;
      z-index: 2;
    }
    &__title {
      font-size: 70px;
      max-width: 650px;
    }
    &__subtitle {
      font-size: 35px;
    }
    &__btn {
      font-size: 35px;
    }
    &__img {
      margin-right: 0;
      margin-left: auto;
      position: relative;
      z-index: 1;
      margin-top: -200px;
      width: 80%;
      max-width: none;
    }
  }
}
