@media (max-width: 767px) {
  .top {
    &__content {
      padding-top: 60px;
    }
    &__title {
      font-size: 50px;
      max-width: 530px;
    }
    &__img {
      margin-top: -130px;
    }
  }
}
@media (max-width: 480px) {
  .top {
    &__content {
      // padding-top: 60px;
    }
    &__title {
      font-size: 30px;
      max-width: 330px;
    }
    &__subtitle {
      font-size: 18px;
    }
    &__btn {
      font-size: 20px;
    }
    &__img {
      margin-top: -100px;
    }
  }
}
